<template>
  <div class="registration" >
    <!-- <br> -->
    <el-button class="filter-item" type="success" @click="handleCreate()"></el-button>
    <div class="content">
      <el-table  :data="tableData"  style="width: 100%" >
        <el-table-column  label=""  width="100">
        <template slot-scope="scope">
          {{scope.$index}}
        </template>
        </el-table-column>
        <el-table-column  prop="style" label=""  width="100"></el-table-column>
        <el-table-column  prop="relate" label=""  width="180"></el-table-column>
        <el-table-column  prop="price" label=""  width="130"></el-table-column>
        <el-table-column  prop="usestate" label=""  width="100"></el-table-column>

        <el-table-column label="">
          <template slot-scope="scope">
            <el-button size="small"  @click="handleEdit(scope.$index, scope.row)"> </el-button>
            <el-button size="small" type="danger"  @click="handleDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--  -->
    <el-dialog title="" v-model="dialogFormVisible" size="tiny" >
      <el-form ref="form" :model="form" label-width="100px">
        <el-form-item label="">
          <el-input v-model="form.style"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.relate"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.price"></el-input>
        </el-form-item>
        <el-form-item label="">
        <el-select v-model="form.usestate" placeholder="">
          <el-option label="ture" value="ture"></el-option>
          <el-option label="false" value="false"></el-option>
        </el-select>
        </el-form-item>
        <el-form-item class="center">
          <el-button type="primary" @click="handleSave" :loading="editLoading"></el-button>
          <el-button @click="dialogFormVisible = false"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!--  -->
    <el-dialog title="" v-model="dialogFormVisibleadd" size="tiny" >
      <el-form ref="form" :model="form" label-width="100px">
        <el-form-item label="">
          <el-input v-model="form.style"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.relate"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.price"></el-input>
        </el-form-item>
        <el-form-item label="">
        <el-select v-model="form.usestate" placeholder="">
          <el-option label="ture" value="ture"></el-option>
          <el-option label="false" value="false"></el-option>
        </el-select>
        </el-form-item>
        <el-form-item class="center">
          <el-button type="primary" @click="handleSaveadd" :loading="editLoading"></el-button>
          <el-button @click="dialogFormVisibleadd = false"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <!--  -->
    <div class="block">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage1"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="100"
        layout="total, sizes, prev, pager, next, jumper"
        :total="tableData.length">
      </el-pagination>
    </div>
  </div>
</template>
<script >

export default {
  name: 'registration',
  data () {
    return {
      msg: '>',
      dialogFormVisible: false,
      dialogFormVisibleadd: false,
      editLoading: false, // 
      currentPage1: 5,
      activeIndex: '1',
      formInline: {
        user: '',
        region: '',
        state: ''
      },
      form: {
        style: '',
        relate: '',
        price: '',
        usestate: ''
      },
      tableData: [{
        style: '',
        relate: '',
        price: '120',
        usestate: 'true'
      }, {
        style: '',
        relate: '',
        price: '1200',
        usestate: 'false'
      }, {
        style: '',
        relate: '',
        price: '120',
        usestate: 'true'
      }, {
        style: '',
        relate: '',
        price: '120',
        usestate: 'true'
      }]
    }
  },
  methods: {
    onSubmit () {
      this.$message('~')
    },

    handleEdit (index, row) {
      this.dialogFormVisible = true
      this.form = Object.assign({}, row)
      this.table_index = index
    },

    handleSave () {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        cancelButtonClass: 'cancel'
      }).then(() => {
        this.editLoading = true// 
        this.tableData[this.table_index] = this.form
        this.tableData.splice(this.table_index, 1, this.form)
        this.$message({
          message: '',
          type: 'success'
        })
        this.editLoading = false
        this.dialogFormVisible = false
      })
    },

    handleSaveadd () {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        cancelButtonClass: 'cancel'
      }).then(() => {
        // this.editLoading = true;//
        let vm = this
        vm.tableData.push(vm.form)
        this.$message({
          message: '',
          type: 'success'
        })
        this.dialogFormVisibleadd = false
      })
    },

    handleDelete (index, row) {
      this.tableData.splice(index, 1)
      this.$message({
        message: '',
        type: 'success'
      })
    },

    handleCreate () {
      this.initform()// //
      this.dialogFormVisibleadd = true
    },

    // 
    initform () {
      this.form = {
        style: '',
        relate: '',
        price: '',
        usestate: ''
      }
    },

    handleSizeChange (val) {
      console.log(` ${val} `)
    },

    handleCurrentChange (val) {
      console.log(`: ${val}`)
    },
    handleSelect (key, keyPath) {
      console.log(key, keyPath)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >
.registration{
  padding: 20px;
}

.registration .content{
 /* width: 95%;*/
  height: 400px;
  background: white;
 /* margin: 0 auto;*/
}
.registration .block{
  text-align: center;
}
.registration .center{
  text-align: center;
}

</style>



