<template>
  <div class="app_second">
      <!--  -->
      <div class="table1">

          <!--  -->
        <div class="btns">
          <el-button type="success" @click="dialogFormVisible = true" ></el-button>
          <el-button type="success" @click="add_catchUp"></el-button>
          <el-button class="btn_edit btn_foot" @click="edit"></el-button>
          <el-button type="danger" @click="delete_work_content()"></el-button>
        </div>

        <!--  -->
        <el-dialog title="" :visible.sync="dialogFormVisible">

            <!--  -->
            <el-form :model="form">

                <!--  -->
                <el-form-item label=":" :label-width="formLabelWidth">
                    <el-select v-model="form.office_selected" placeholder="" style="width:100px;">
                        <el-option v-for="(item, index) in form.office" :value="item.text" v-bind:key="index">
                        </el-option>
                    </el-select>
                </el-form-item>

            <!--  -->
            <el-form-item label=":" :label-width="formLabelWidth">
                    <el-select v-model="form.doctor_selected"  style="width:100px;">
                        <el-option v-for="(item,index) in form.doctor" :value="item.name" v-bind:key="index">
                        </el-option>
                    </el-select>
                </el-form-item>

                <!--  -->
                <el-form-item label=":" :label-width="formLabelWidth">
                    <el-select v-model="form.register_selected"  style="width:100px;">
                        <el-option v-for="item in form.register" :value="item.type" v-bind:key="item">
                        </el-option>
                    </el-select>
                </el-form-item>

                <!--  -->
                <el-form-item label=":" :label-width="formLabelWidth">
                    <el-select v-model="form.time[0].time_long"  style="width:100px;">
                        <el-option v-for="item in form.time" :value="item.time_long" v-bind:key="item">
                        </el-option>
                    </el-select>
                </el-form-item>

                <!--  -->
                <el-form-item>
                    <el-checkbox v-model="form.checked">
                        (App)
                        <br>
                        <i style="opacity:.7;"></i>
                    </el-checkbox>
                </el-form-item>

                <!--  -->
                <el-form-item label=":">

                    <!--  -->
                    <el-date-picker v-model="form.time_selected" type="date" placeholder="" :picker-options="form.pickerOptions0"></el-date-picker>

                    <!--  -->
                    <el-time-select placeholder="" v-model="form.startTime" :picker-options="{
                            start: '08:30',
                            step: '00:15',
                            end: '18:30'
                          }">
                    </el-time-select>
                    <el-time-select placeholder="" v-model="form.endTime" :picker-options="{
                          start: '08:30',
                          step: '00:15',
                          end: '18:30',
                          minTime: form.startTime
                        }">
                    </el-time-select>
                </el-form-item>
            </el-form>

            <!--  -->
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false"> </el-button>
                <el-button type="primary" @click="add"> </el-button>
            </div>
        </el-dialog>

      <!--  -->
        <el-dialog title="" :visible.sync="edit_dialogFormVisible">

            <!--  -->
            <el-form :model="form">

            <!--  -->
                <el-form-item label=":" :label-width="formLabelWidth">
                    {{ form.register_selected }}
                </el-form-item>

                <!--  -->
                <el-form-item label=":" :label-width="formLabelWidth">
                    {{ form.office_selected }}
                </el-form-item>

            <!--  -->
            <el-form-item label=":" :label-width="formLabelWidth">
                    {{ form.doctor_selected }}
                </el-form-item>

            <!--  -->
                <el-form-item>
                    <el-checkbox v-model="form.checked">
                        (App)
                        <br>
                        <i style="opacity:.7;"></i>
                    </el-checkbox>
                </el-form-item>

                <!--  -->
                <el-form-item label=":">
                    {{ form.year_selected }}-
                    {{ form.mon_selected }}-
                    {{ form.day_selected }}
                    <br>
                    <!--  -->
                    <el-time-select placeholder="" v-model="form.startTime" :picker-options="{
                            start: '08:30',
                            step: '00:15',
                            end: '18:30'
                          }">
                    </el-time-select>
                    <el-time-select placeholder="" v-model="form.endTime" :picker-options="{
                          start: '08:30',
                          step: '00:15',
                          end: '18:30',
                          minTime: form.startTime
                        }">
                    </el-time-select>
                  </el-form-item>

            </el-form>

            <!-- - -->
            <div slot="footer" class="dialog-footer">
                <el-button @click="edit_dialogFormVisible = false"> </el-button>
                <el-button type="primary" @click="edit_sure"> </el-button>
            </div>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="add_catchUp_dialogFormVisible">

            <!--  -->
            <el-form :model="form">

            <!--  -->
            <el-form-item>
                    
                </el-form-item>

                <!--  -->
                <el-form-item label=":" :label-width="formLabelWidth">
                    <el-select v-model="form.office_selected" placeholder="" style="width:100px;">
                        <el-option v-for="item in form.office" :value="item.text" v-bind:key="item">
                        </el-option>
                    </el-select>
                </el-form-item>

            <!--  -->
            <el-form-item label=":" :label-width="formLabelWidth" >
                    <el-select v-model="form.doctor_selected"  style="width:100px;">
                        <el-option v-for="(item,index) in form.doctor" :value="item.name" v-bind:key="index">
                        </el-option>
                    </el-select>
                </el-form-item>

                <!--  -->
                <el-form-item label=":" :label-width="formLabelWidth">
                    <el-select v-model="form.register_selected"  style="width:100px;">
                        <el-option v-for="item in form.register" :value="item.type" v-bind:key="item">
                        </el-option>
                    </el-select>
                </el-form-item>

                <!--  -->
                <el-form-item label=":" :label-width="formLabelWidth">
                    <el-select v-model="form.time[0].time_long"  style="width:100px;">
                        <el-option v-for="item in form.time" :value="item.time_long" v-bind:key="item">
                        </el-option>
                    </el-select>
                </el-form-item>

                <!--  -->
                <el-form-item>
                    <el-checkbox v-model="form.checked">
                        (App)
                        <br>
                        <i style="opacity:.7;"></i>
                    </el-checkbox>
                </el-form-item>

                <!--  -->
                <el-form-item label=":">

                    <!--  -->
                    <el-date-picker v-model="form.time_selected" type="date" placeholder="" :picker-options="form.pickerOptions0"></el-date-picker>

                    <!--  -->
                    <el-time-select placeholder="" v-model="form.startTime" :picker-options="{
                            start: '08:30',
                            step: '00:15',
                            end: '18:30'
                          }">
                    </el-time-select>
                    <el-time-select placeholder="" v-model="form.endTime" :picker-options="{
                          start: '08:30',
                          step: '00:15',
                          end: '18:30',
                          minTime: form.startTime
                        }">
                    </el-time-select>
                </el-form-item>
            </el-form>

            <!--  -->
            <div slot="footer" class="dialog-footer">
                <el-button @click="add_catchUp_dialogFormVisible = false"> </el-button>
                <el-button type="primary" @click="add_catchUp_sure"> </el-button>
            </div>
        </el-dialog>

        <!--  -->
        <table border="1" cellspacing="0">
          <tr>
            <td v-for = "(item,index) in weeks" v-bind:key="item">
              {{ item.value }}
              <br>
              {{ month1 }}-{{ day1+index }}
            </td>
          </tr>
          <tr class="td_65px">
            <td v-for="(item,week_index) in weeks_content" v-bind:key="week_index">
              <span v-for="(item1,todo_index) in item.todos" v-bind:key="todo_index" @click="find_index(week_index,todo_index)" v-bind:class="{ add_catchUp_myClass : item1.active , findIndex_myClass : item1.findIndex_active }">
                <br>
                {{ item1.text }}
                <br>
                {{ item1.time_start }}
                {{ item1.time_end }}
              </span>
            </td>
          </tr>
      </table>

      </div>
  </div>
</template>
<style type="text/css">
  .app_second .table1
  {
    width: 941px;
    height: 500px;
    position: absolute;
    margin: 57px 26px;
    background: #ffffff;
  }
  .app_second table
  {
    clear: left;
    border-color:rgba(255,255,255,.5);
    background: #ffffff;
    width: 850px;
    margin: 27px 48px;

  }
  .app_second table
  {
    text-align: center;
  }
  .app_second table tr td span
  {
    background:#cae67a;
    width:120px;
    heigth:60px;
    display:block;
  }
  .app_second .td_65px
  {
    height: auto;
    opacity: .7;
  }
  .app_second .btns
  {
    margin-left:35%;
    margin-top: 20px;
  }
  .app_second .btn_foot
  {
    margin-left: 18px;
    border: none;
    width: 80px;
    height: 30px;
    background: #f4f4f4;
  }
  .app_second .btn_add
  {
    background: green;
    color: #ffffff;
  }
  .app_second .btn_delete
  {
    background: #970f11;
    color: #ffffff;
  }
  .app_second table tr td span.add_catchUp_myClass
  {
    background: #e3b100;
  }
  .app_second table tr td span.findIndex_myClass
  {
    background: #0172d0;
  }
</style>
<script>
import store from '../../store'
var myDate1 = new Date()
export default {
  data () {
    return {
      weekText: '',
      pickerOptions0: {
        disabledDate (time) {
          return time.getTime() < Date.now() - 8.64e7
        }
      },
      month1: myDate1.getMonth() + 1,
      day1: myDate1.getDate(),
      // 
      options: [{
        value: '1',
        label: ''
      }, {
        value: '2',
        label: ''
      }, {
        value: '3',
        label: ''
      }, {
        value: '4',
        label: ''
      }, {
        value: '5',
        label: ''
      }],
      value: '',
      // 
      checked: true,
      weeks:
      [
        { value: '' },
        { value: '' },
        { value: '' },
        { value: '' },
        { value: '' },
        { value: '' },
        { value: '' }
      ],
      // 7
      active: false,
      findIndex_active: false,
      weeks_content: store.state.weeks_content,
      // [
      // { todos: [{text:"" , time_start:"" , time_end:"" , active:false ,findIndex_active : false}] },
      // { todos: [{text:"" , time_start:"" , time_end:"" , active:false ,findIndex_active : false}] },
      // { todos: [{text:"" , time_start:"" , time_end:"" , active:false ,findIndex_active : false}] },
      // { todos: [{text:"" , time_start:"" , time_end:"" , active:false ,findIndex_active : false}] },
      // { todos: [{text:"" , time_start:"" , time_end:"" , active:false ,findIndex_active : false}] },
      // { todos: [{text:"" , time_start:"" , time_end:"" , active:false ,findIndex_active : false}] },
      // { todos: [{text:"" , time_start:"" , time_end:"" , active:false ,findIndex_active : false}] }
      // ],
      // 
      todo_index: '',
      week_index: '',
      dialogFormVisible: false,
      edit_dialogFormVisible: false,
      add_catchUp_dialogFormVisible: false,
      form: {
        office:
        [
          { text: '' },
          { text: '' },
          { text: '' },
          { text: '' },
          { text: '' }
        ],
        office_selected: '',
        doctor:
        [
          { name: '' },
          { name: '' },
          { name: '' },
          { name: '' },
          { name: '' }
        ],
        doctor_selected: '',
        register:
        [
          { type: '' },
          { type: '' },
          { type: '' },
          { type: '' },
          { type: '' }
        ],
        register_selected: '',
        time:
        [
          { time_long: '20' },
          { time_long: '30' },
          { time_long: '40' },
          { time_long: '50' },
          { time_long: '60' }
        ],
        pickerOptions0: {
          disabledDate (time) {
            return time.getTime() < Date.now() - 8.64e7
          }
        },
        time_selected: Date.now(),
        year_selected: '',
        mon_selected: '',
        day_selected: '',
        region: '',
        checked: true,
        startTime: '',
        endTime: ''
      },

      formLabelWidth: '80px'
    }
  },
  mounted: function () {
    // window.localStorage.setItem("weeks_contentValue",JSON.stringify(this.weeks_content));

    // this.weeks_content=JSON.parse(window.localStorage.getItem("weeks_contentValue"));
    // console.log("bbbbbbbb",this.weeks_content);
  },
  methods: {
    add: function () {
      // var vm = this
      this.dialogFormVisible = false
      console.log(this.form.doctor_selected)
      console.log('---', new Date())
      console.log('---', this.form.time_selected)

      console.log('---', new Date(this.form.time_selected).getTime())
      console.log('---:', new Date(this.form.time_selected).getMonth() + 1)
      console.log('---:', new Date(this.form.time_selected).getDate())
      console.log('---:', this.month1)
      console.log('---:', this.day1)

      console.log('---:', this.form.startTime)
      console.log('---:', this.form.endTime)

      var year = new Date(this.form.time_selected).getFullYear()
      var mon = new Date(this.form.time_selected).getMonth() + 1
      var day = new Date(this.form.time_selected).getDate()
      this.form.year_selected = year
      this.form.mon_selected = mon
      this.form.day_selected = day
      for (let i = 0; i < 7; i++) {
        if (mon === this.month1 && day === (this.day1 + i)) {
          // (text ===""),pop
          if (this.weeks_content[i].todos[0].text === '') {
            this.weeks_content[i].todos.pop()
          }

          var a = this.form.doctor_selected
          var b = this.form.startTime
          var c = this.form.endTime
          this.weeks_content[i].todos.push({ text: a, time_start: b, time_end: c, active: false, findIndex_active: false })
          // window.localStorage.setItem("weeks_contentValue",JSON.stringify(this.weeks_content));
        }
      }
      // localStorage.clear();
      // this.weeks_content=JSON.parse(window.localStorage.getItem("weeks_contentValue"));
      // var itemss = JSON.parse(window.localStorage.getItem("weeks_contentValue"));
      // console.log("itemss------------",itemss);
    },
    find_index: function (weekIndex, todoIndex) {
      console.log('weeks', weekIndex)
      console.log('todos', todoIndex)
      var restWeekIndex = this.weekIndex
      var restTodoIndex = this.todoIndex
      this.week_index = weekIndex
      this.todo_index = todoIndex

      // 
      if (restWeekIndex !== '' && restTodoIndex !== '') {
        this.weeks_content[restWeekIndex].todos[restTodoIndex].findIndex_active = false
      }
      // 
      this.weeks_content[weekIndex].todos[todoIndex].findIndex_active = true
    },
    delete_work_content: function () {
      console.log(this.week_index, this.todo_index)
      this.weeks_content[this.week_index].todos.splice(this.todo_index, 1)
      // this.weeks_content[this.week_index].todos[this.todo_index].pop();
      // this.list.splice(index,1);
      // var t = this.delete_work();
      // console.log(index);
      // console.log(this.delete_work())
      // var t = this.delete_work();
      // console.log(t);
      if (this.weeks_content[this.week_index].todos.length <= 0) {
        this.weeks_content[this.week_index].todos.push({ text: '', time_start: '', time_end: '', active: false, findIndex_active: false })
      }
    },
    edit: function () {
      console.log('weeks_content', this.week_index)
      console.log('todos', this.todo_index)
      console.log('', this.form.register_selected)
      console.log('', this.form.office_selected)
      console.log('', this.form.doctor_selected)
      console.log('', this.form.time_selected)
      console.log('', this.form.startTime)
      console.log('', this.form.endTime)
      this.edit_dialogFormVisible = true
      if (this.weeks_content[this.week_index].todos[this.todo_index].text === '') {
        this.edit_dialogFormVisible = false
      }
    },
    edit_sure: function () {
      this.weeks_content[this.week_index].todos[this.todo_index].time_start = this.form.startTime
      this.weeks_content[this.week_index].todos[this.todo_index].time_end = this.form.endTime
      this.edit_dialogFormVisible = false
    },
    add_catchUp: function () {
      this.add_catchUp_dialogFormVisible = true
    },
    add_catchUp_sure: function () {
      var addCatchUpYear = new Date(this.form.time_selected).getFullYear()
      var addCatchUpMon = new Date(this.form.time_selected).getMonth() + 1
      var addCatchUpDay = new Date(this.form.time_selected).getDate()
      this.form.year_selected = addCatchUpYear
      this.form.mon_selected = addCatchUpMon
      this.form.day_selected = addCatchUpDay
      for (let i = 0; i < 7; i++) {
        if (addCatchUpMon === this.month1 && addCatchUpDay === (this.day1 + i)) {
          // (text ===""),pop
          if (this.weeks_content[i].todos[0].text === '') {
            this.weeks_content[i].todos.pop()
          }

          var addcatchUpa = this.form.doctor_selected
          var addcatchUpb = this.form.startTime
          var addcatchUpc = this.form.endTime
          this.weeks_content[i].todos.push({ text: addcatchUpa, time_start: addcatchUpb, time_end: addcatchUpc, active: true, findIndex_active: false })
          // this.active = true;
        }
      }
      this.add_catchUp_dialogFormVisible = false
    }
  }
}

</script>



