<template>
  <div class="style" >
    <br>
    <el-form :inline="true" class="demo-form-inline">
      <template>
      <!-- <template scope="scope"> -->
        <el-button type="success" @click="add_account_style()"></el-button>
      </template>
    </el-form>
    <!--  -->
    <div class="content">
      <el-table  :data="tableData"  style="width: 100%" >
        <el-table-column  prop="account_type" label=""  width="180"></el-table-column>
        <el-table-column  prop="avaliable_date" label=""  width="180"></el-table-column>
        <el-table-column  prop="username" label=""  width="180"></el-table-column>
        <el-table-column  prop="date" label=""  width="180"></el-table-column>
        <el-table-column  prop="edit_date" label=""  width="180"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button size="small" class="font-color" ><i class="fa fa-book" aria-hidden="true"></i></el-button>
            <el-button size="small" class="font-color" @click="add_account_change(scope.$index,scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--  -->
    <el-dialog title="" v-model="dialogFormVisible" size="tiny" >
      <el-form ref="form" :model="form" label-width="120px">

        <el-form-item label="">
          <el-input v-model="form1.account_type"  ></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-input v-model="form1.avaliable_date"  ></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-input v-model="form1.username"  ></el-input>
        </el-form-item>

        <el-form-item class="center">
          <template>
          <!-- <template scope="scope"> -->
            <el-button type="primary" @click="handleSaveadd()" :loading="editLoading"></el-button>
            <el-button  @click="dialogFormVisible = false"></el-button>
          </template>
        </el-form-item>

      </el-form>
    </el-dialog>
     <!--  -->
    <el-dialog title="" v-model="dialogFormVisible_change" size="tiny" >
      <el-form ref="form" :model="form" label-width="120px">

        <el-form-item label="">
          <el-input v-model="form.account_type" ></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-input v-model="form.avaliable_date"  ></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-input v-model="form.username"  ></el-input>
        </el-form-item>

        <el-form-item class="center">
          <template>
          <!-- <template scope="scope"> -->
            <el-button type="primary" @click="handleSavechange" :loading="editLoading"></el-button>
            <el-button  @click="dialogFormVisible_change = false"></el-button>
          </template>
        </el-form-item>

      </el-form>
    </el-dialog>
    <!--  -->
    <div class="block">
       <el-pagination
         @size-change="handleSizeChange"
         @current-change="handleCurrentChange"
         :current-page="currentPage1"
         :page-sizes="[10, 20, 30, 40]"
         :page-size="100"
         layout="total, sizes, prev, pager, next, jumper"
         :total="tableData.length">
       </el-pagination>
    </div>
  </div>
</template>

<script >
import {api} from '../../global/api'
export default {
  name: 'style',
  data () {
    return {

      dialogFormVisible: false,
      dialogFormVisible_change: false,
      editLoading: false, // 
      currentPage1: 5,
      activeIndex: '1',
      currentdate: '',
      tableData: [{
        account_type: '',
        account: '',
        username: '',
        date: '',
        avaliable_date: '',
        edit_date: ''
      }],
      formInline: {
        account_type: '',
        username: '',
        date: '',
        avaliable_date: '',
        account: '',
        style: '',
        edit_date: ''
      },
      form: {
        account_type: '',
        username: '',
        date: '',
        avaliable_date: '',
        account: '',
        style: '',
        edit_date: ''
      },
      form1: {
        account_type: '',
        username: '',
        date: '',
        avaliable_date: '',
        account: '',
        style: '',
        edit_date: ''
      }
    }
  },
  // 
  mounted: function () {
    this.$http.get(api.style).then(function (response) {
      this.tableData = response.data.tableData
    })
  },

  methods: {
    onSubmit () {
      this.$message('~')
    },
    // xxxx-xx-xx xx:xx
    CurentTime () {
      var now = new Date()

      var year = now.getFullYear() // 
      var month = now.getMonth() + 1 // 
      var day = now.getDate() // 

      var hh = now.getHours() // 
      var mm = now.getMinutes() // 
      var ss = now.getSeconds() // 

      var clock = year + '-'

      if (month < 10) { clock += '0' }

      clock += month + '-'

      if (day < 10) { clock += '0' }

      clock += day + ' '

      if (hh < 10) { clock += '0' }

      clock += hh + ':'
      if (mm < 10) clock += '0'
      clock += mm + ':'

      if (ss < 10) clock += '0'
      clock += ss
      return (clock)
    },
    // 
    add_account_style () {
      this.dialogFormVisible = true
      this.form1.date = this.CurentTime()// 
      this.form1.edit_date = this.CurentTime()
    },
    // 
    add_account_change (index, row) {
      this.dialogFormVisible_change = true
      this.form = Object.assign({}, row)
      this.table_index = index
    },
    // 
    handleSaveadd () {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        cancelButtonClass: 'cancel'
      }).then(() => {
        let vm = this.form1

        this.tableData.push(vm)
        this.form1 = {
          account_type: '',
          username: '',
          date: '',
          avaliable_date: '',
          account: '',
          style: '',
          edit_date: ''
        }
        this.$message({
          message: '',
          type: 'success'
        })
        this.dialogFormVisible = false
        // 
      })
    },
    // 
    handleSavechange () {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        cancelButtonClass: 'cancel'
      }).then(() => {
        this.editLoading = true// 
        this.form.edit_date = this.CurentTime()

        this.tableData[this.table_index] = this.form
        this.tableData.splice(this.table_index, 1, this.form)
        this.$message({
          message: '',
          type: 'success'
        })
        this.editLoading = false
        this.dialogFormVisible_change = false
      })
      console.log(this.form)
    },
    handleSizeChange (val) {
      console.log(` ${val} `)
    },

    handleCurrentChange (val) {
      console.log(`: ${val}`)
    },
    handleSelect (key, keyPath) {
      console.log(key, keyPath)
    },
    pickerOptions0: {
      disabledDate (time) {
        return time.getTime() < Date.now() - 8.64e7
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >
    .style{
      padding: 20px;
    }
    .style .content{
      height: 400px;
      background: white;
    }
    .style .block{
      text-align: center;
    }
    .style .center{
      text-align: center;
    }
    .style .font-color{
      color: #50bfff;
      border:0px solid #50bfff;
    }
    .style .left{
      float: left;
      text-align: center;
      width: 50%;
    }
    .style .right{
      float: right;
      text-align: center;
      width: 50%;
    }
    .style .center{
      text-align: center;
    }
</style>



