<template>
  <div class="inOne-records">
    <div class="inOne-TabRecords">
      <h3>:</h3>
      <p>............</p>
    </div>
    <form>
      <div class="inOne-TabRecords" v-for="item in formData" v-bind:key="item">
        <h3>{{ item.text }}</h3>
        <textarea placeholder="....." v-model="item.checkText"></textarea>
      </div>
    </form>
    <div class="inOne-TabBttom">
          <el-button  @click="saveData()" type="primary"><i class="fa fa-floppy-o"></i></el-button>
          <el-button @click="resetData()" type="primary"><i class="fa fa-pencil" aria-hidden="true"></i></el-button>
        </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      formData: [
        {text: ':', checkText: ''},
        {text: ':', checkText: ''},
        {text: ':', checkText: ''},
        {text: ':', checkText: ''}
      ]
    }
  },
  methods: {
    saveData () {
      let that = this
      // console.log('row',index,'-----',row);
      that.$confirm('', '', {}).then(() => {
        that.$message({
          type: 'success',
          message: ''
        })
        console.log(JSON.stringify(this.formData))
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    resetData () {
      let that = this
      // console.log('row',index,'-----',row);
      that.$confirm('', '', {}).then(() => {
        that.$message({
          type: 'success',
          message: ''
        })
        that.formData = [
          {text: ':', checkText: ''},
          {text: ':', checkText: ''},
          {text: ':', checkText: ''},
          {text: ':', checkText: ''}
        ]
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
  }
}
</script>

<style>
  .inOne-TabRecords{
      padding-top: 30px;
      padding-bottom: 30px;
      border-bottom: 1px solid #dcdcdc;
    }
  .inOne-NoRecords{
      border-bottom: none;
  }
    .inOne-TabRecords h3,.inOne-TabRecords p{
      margin: 0;
    }
    .inOne-TabRecords p{
      margin-left: 20px;
    }
    .inOne-TabRecords h3{
      float: left;
      font-size: 18px;
      font-weight: normal;
      width: 10%;
    }
    .inOne-TabRecords p{
      width: 75%;
      display: inline-block;
      white-space: pre-wrap;
      color: #646a6b;
    }
    .inOne-TabRecords textarea{
      width: 80%;
      display: inline-block;
      white-space: pre-wrap;
      color: #646a6b;
      height: 100px;
      margin-left: 20px;
    }
    .inOne-TabBttom{
      text-align: center;
    }
    .inOne-TabBttom h2{
      color: blue;
      padding-left: 0;
      margin-right: 20px;
      cursor: pointer;
    }
</style>



