<template>
  <div class="inOne-Checks">
    <el-table :data="tableData" style="width: 100%" :default-sort="{prop: 'date', order: 'descending'}">
      <el-table-column prop="type" label="" width="180">
      </el-table-column>
      <el-table-column prop="deparment" label="" width="100">
      </el-table-column>
      <el-table-column prop="deparment" label="" width="100">
      </el-table-column>
      <el-table-column label="" width="350">
        <template slot-scope="props">
          <el-input v-model="props.row.attention"></el-input>
        </template>
      </el-table-column>
      <el-table-column prop="prices" label="()" align="center" sortable>
      </el-table-column>
    </el-table>
    <div class="checeksub">
      <el-button type="primary" @click="onload()"></el-button>
    </div>
  </div>
</template>

<style>
  .checeksub{
    margin-top: 10px;
  }
</style>

<script>
import Vue from 'vue'
import {api} from '../../global/api'
export default {
  data () {
    return {
      tableData: []
    }
  },
  methods: {
    onload () {
      let that = this
      // console.log('row',index,'-----',row);
      that.$confirm('', '', {}).then(() => {
        that.$message({
          type: 'success',
          message: ''
        })
        console.log(JSON.stringify(this.tableData))
      }).catch(() => {
      })
    }
  },
  mounted () {
    let that = this
    Vue.http.get(api.inquiryData).then(response => {
      console.log('')
      console.log(response)

      let formdata = response.data.tableDatas
      console.log('json', formdata)
      that.tableData = formdata
    }, response => {
      alert('')
    })
  }
}
</script>



