<template>
  <div class="personal">
    <div class="personal-content">
      <div class="content-left">
        <i class="Img">
          <img src="../../assets/logo.png">
        </i>
        <p >{{ per.name }}</p>
      </div>
      <div class="content-right">
        <div class="personal-info">
          <div class="title">
            <span></span>
            <el-button type="text" class="editor" @click="editorDialogForm">
              <i class="fa fa-pencil-square-o" aria-hidden="true"></i>
              
            </el-button>
            <!--  -->
            <el-dialog title="" :visible.sync="dialogFormVisible" >
                <el-form :model="form" :rules="rules" ref="form">
                  <el-form-item label=":" :label-width="formLabelWidth">
                      
                  </el-form-item>
                  <el-form-item label=":" :label-width="formLabelWidth">
                      <el-select v-model="form.subordinate" placeholder="" style="width:220px">
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                      </el-select>
                  </el-form-item>
                  <el-form-item label=":" :label-width="formLabelWidth">
                      <el-input v-model="name1" auto-complete="off" style="width:220px"></el-input>
                  </el-form-item>
                  <el-form-item label="" :label-width="formLabelWidth">
                      <el-radio class="radio" v-model="form.sex" label=""></el-radio>
                    <el-radio class="radio" v-model="form.sex" label=""></el-radio>
                  </el-form-item>
                  <el-form-item label=":" :label-width="formLabelWidth">
                      <el-input v-model="form.age"  auto-complete="off" style="width:220px"></el-input>
                  </el-form-item>
                  <el-form-item label=":" :label-width="formLabelWidth" prop="phone">
                      <el-input v-model="form.phone"  auto-complete="off" style="width:220px"></el-input>
                  </el-form-item>
                <el-form-item label=":" :label-width="formLabelWidth">
                      <el-input v-model="form.job" auto-complete="off" style="width:220px"></el-input>
                  </el-form-item>
                  <el-form-item label=":" :label-width="formLabelWidth">
                      <el-input type="textarea" :rows="4" placeholder="" v-model="form.note" style="width:350px">
                      </el-input>
                  </el-form-item>
                </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false"> </el-button>
                <el-button type="primary" @click="handleDialogForm"> </el-button>
              </div>
            </el-dialog>

          </div>
          <div class="title-content">
            <span class="left">
              <span></span>
              <span class="change-text">{{ per.name }}</span>
            </span>
            <span class="left">
              <span></span>
              <span class="change-text">{{ personal.sex }}</span>
            </span>
          </div>
          <div class="title-content">
            <span class="left">
              <span></span>
              <span class="change-text">{{ personal.age }}</span>
            </span>
            <span class="left">
              <span></span>
              <span class="change-text">{{ personal.subordinate }}</span>
            </span>
          </div>
          <div class="title-content">
            <span class="left">
              <span></span>
              <span class="change-text">{{ personal.job }}</span>
            </span>
            <span class="left">
              <span></span>
              <span class="change-text">{{ personal.phone }}</span>
            </span>
          </div>
        </div>
        <div class="personal-info special">
          <div class="title">
            <span></span>
          </div>
          <div class="title-content">
            <span class="left">
              <span></span>
              <span class="change-text">{{ loginForm.userName }}</span>
            </span>
          </div>
          <div class="title-content">
            <span class="left">
              <span></span>
              <span class="change-text">{{ personal3.email }}</span>
            </span>
            <el-button type="text" class="editor editor1" @click="editorDialogForm3">
              <i class="fa fa-pencil-square-o" aria-hidden="true"></i>
              
            </el-button>
            <el-dialog title="" :visible.sync="dialogFormVisible3">
              <el-form :model="form3" :rules="rules">
                  <el-form-item label=":" :label-width="formLabelWidth">
                    <span v-if="personal3.email === '-'">
                      {{ personal3.nEmail }}
                    </span>
                    <span v-else>
                      {{ personal3.email }}
                    </span>
                  </el-form-item>
                  <el-form-item label=":" :label-width="formLabelWidth" prop="email">
                    <el-input v-model="form3.email" auto-complete="off" style="width:220px"></el-input>
                  </el-form-item>
              </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible3 = false"> </el-button>
                <el-button type="primary" @click="handleDialogForm3"> </el-button>
              </div>
            </el-dialog>

          </div>
        </div>
        <div class="personal-info special">
          <div class="title">
            <span></span>
            <el-button type="text" class="editor" @click="editorDialogForm2">
              <i class="fa fa-pencil-square-o" aria-hidden="true"></i>
              
            </el-button>
            <el-dialog title="" :visible.sync="dialogFormVisible2">
              <el-form :model="form2">
                  <el-form-item label=":" :label-width="formLabelWidth">
                    {{ per.name }}
                  </el-form-item>
                  <el-form-item label=":" :label-width="formLabelWidth">
                    {{ptype }}
                  </el-form-item>
                  <el-form-item label=":" :label-width="formLabelWidth">
                    <el-select v-model="form2.visitSubordinate" placeholder="" style="width:220px">
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                      </el-select>
                  </el-form-item>
                  <el-form-item label=":" :label-width="formLabelWidth">
                        <el-input v-model="form2.disease" auto-complete="off" style="width:220px"></el-input>
                    </el-form-item>
                    <el-form-item label=":" :label-width="formLabelWidth">
                        <el-input type="textarea" :rows="4" v-model="form2.introduce" style="width:350px">
                        </el-input>
                    </el-form-item>
              </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible2 = false"> </el-button>
                <el-button type="primary" @click="handleDialogForm2"> </el-button>
              </div>
            </el-dialog>
          </div>
          <div class="title-content">
            <span class="left">
              <span></span>
              <span class="change-text">{{ ptype }}</span>
            </span>
          </div>
          <div class="title-content">
            <span class="left">
              <span></span>
              <span class="change-text">{{ personal2.visitSubordinate }}</span>
            </span>
          </div>
          <div class="title-content">
            <span class="left1">
              <span></span>
              <span class="change-text">{{ personal2.disease }}</span>
            </span>
          </div>
          <div class="title-content">
            <span class="left1">
              <span></span>
              <span class="change-text">{{ personal2.introduce }}</span>
            </span>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>
<style scoped>
  .personal .navBar{
    display: inline-block;
    color: #f1f1f1;
  }
  .personal .content-left,.personal .content-right{
    display: inline-block;
    text-align: left;
  }
  .personal .content-left{
    width: 300px;
    height: 100%;
    text-align: center;
    position: relative;
    top: -500px;
  }
  .personal .content-left img{
    width: 80px;
    height: 80px;
    vertical-align: middle;
  }
  .personal .content-left p{
    padding-left: 14px;
  }
  .personal .content-right{
    color: #AEAEAE;
    width: 700px;
  }
  .personal .content-right .personal-info{
    border: 1px solid #f1f1f1;
    border-radius: 10px;
    background: #fff;
    height: 100%;
  }
  .personal .content-right .special{
    margin-top: 15px;
  }
  .personal .content-right .personal-info .title{
    color: #666;
    font-size: 18px;
    line-height: 50px;
    border-bottom: 1px solid #f1f1f1;
  }
  .personal .content-right .personal-info .title span{
    width: 50%;
    text-align: left;
    padding-left: 25px;
  }
  .personal .content-right .personal-info .title .editor{
    width: 50%;
    text-align: right;
    margin-left: 200px;
    font-size: 18px;
  }
  .personal .content-right .title-content{
    line-height: 50px;
    border:0.5px solid #f1f1f1;
  }
  .personal .content-right .title-content .left{
    display: inline-block;
    width: 40%;
  }
  .personal .content-right .title-content .left1{
    width: 100%;
    display: inline-block;
  }
  .personal .content-right .title-content span:first-child{
    padding-left: 13px;
  }
  .personal .content-right .change-text{
    color: #666;
  }
  .personal .editor1{
    font-size: 18px;
    margin-left: 295px;
  }
</style>
<script>
import store from '../../store'
import {api} from '../../global/api'
export default {
  data () {
    // 
    var validateNum = (rule, value, callback) => {
      if (!value) {
        callback(new Error(''))
      } else {
        console.log('value', value)
        var reg1 = /^[0-9]{11}$/
        if (!reg1.test(value)) {
          callback(new Error('11'))
        } else {
          callback()
        }
      }
    }
    var validateeMail = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else {
        var reg = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+.[a-zA-Z0-9_-]+$/
        if (!reg.test(value)) {
          callback(new Error('@'))
        } else {
          callback()
        }
      }
    }
    return {
      // 
      per: store.state.per,
      user: store.state.userInfo,
      // 
      personal: {
        name:this.user.adminName,
        sex: '',
        age: '',
        subordinate: '',
        job: '',
        phone: this.user.phoneNumber
      },
      ptype: '',
      // 
      personal2: {
        visitSubordinate: '',
        disease: '',
        introduce: ''
      },
      // 
      personal3: {
        email: '',
        nEmail: ''
      },
      // state
      loginForm: store.state.userInfo,
      // 
      dialogFormVisible: false,
      // 
      dialogFormVisible2: false,
      // 
      dialogFormVisible3: false,
      form: {
      // name: '',
        subordinate: '',
        sex: '',
        age: '',
        phone: '',
        job: '',
        note: ''
      },
      form2: {
        visitSubordinate: '',
        disease: '',
        introduce: ''
      },
      form3: {
        email: ''
      },
      formLabelWidth: '120px',
      // 
      rules: {
        phone: [
          { validator: validateNum, trigger: 'blur' }
        ],
        email: [
          { validator: validateeMail, trigger: 'blur' }
        ]
      }
    }
  },
  mounted: function () {
    this.$http.get(api.personal).then(function (response) {
      // console.log("response",response);
      this.personal = response.data.data.personal
      this.personal2 = response.data.data.personal2
      this.personal3 = response.data.data.personal3
      this.ptype = response.data.data.ptype
      // console.log("personal",this.personal);
    })
  },
  methods: {
    // 
    editorDialogForm: function () {
      this.dialogFormVisible = true
      this.form = {
        // name: '',
        subordinate: '',
        sex: '',
        date: '',
        phone: '',
        job: '',
        note: ''
      }
      this.name1 = ''
    },
    // 
    editorDialogForm2: function () {
      this.dialogFormVisible2 = true
      this.form2 = {
        visitSubordinate: '',
        disease: '',
        introduce: ''
      }
    },
    // 
    editorDialogForm3: function () {
      this.dialogFormVisible3 = true
      this.form3 = {
        email: ''
      }
    },
    // 
    handleDialogForm: function () {
      this.dialogFormVisible = false
      // this.personal=this.form;  //
      this.personal = JSON.parse(JSON.stringify(this.form))
      // this.name=JSON.parse(JSON.stringify(this.form.name));  //
      this.per.name = this.name1
    },
    // 
    handleDialogForm2: function () {
      this.dialogFormVisible2 = false
      // this.personal=this.form;  //
      this.personal2 = JSON.parse(JSON.stringify(this.form2)) // 
    },
    // 
    handleDialogForm3: function () {
      this.dialogFormVisible3 = false
      // this.personal=this.form;  //
      this.personal3 = JSON.parse(JSON.stringify(this.form3)) // 
    }

  }
}
</script>



